	}

	.vertical-menu li.openmenu>a {
		color: #000;
	}

	.vertical-menu li.openmenu .switch {
		-webkit-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		-o-transform: rotate(45deg);
		transform: rotate(45deg);
	}

	.vertical-menu li {
		padding: 8px 0;
		position: relative;
		list-style: none;
		@include transition(.2s);

		a {
			&:hover {
				color: $coloraqua;

				i {
					color: $coloraqua;
					opacity: 1;
				}
			}
		}

		i {
			color: #FFF;
			margin-right: 20px;
			opacity: 0.4;
			@include transition(.3s);
		}
	}

	.vertical-menu li a {
		color: #FFF;
		font-family: $fonthead;
		font-size: 24px;
		font-weight: 700;
		width: 100%;
		text-decoration: none;
		position: relative;
		cursor: pointer;

		&.nav-link {
			display: initial;
			padding: 0;
		}
	}

	.vertical-menu li .nav-link.active {
		color: $coloraqua;

		i {
			color: $coloraqua;
			opacity: 1;
		}
	}

	.footer {
		margin-top: auto;
		position: relative;
	}

	.copyright {
		color: #9C9AB3;
		font-size: 14px;
		transform: rotate(-180deg);
		writing-mode: vertical-rl;
		white-space: nowrap;
		display: inline-block;
		overflow: visible;
	}
 
	
}

header.mobile-header-1 {
	background: $colordark;
	display: none;
	padding: 10px 0;
	@include transition(.3s);

	&.open {
		-webkit-transform: translateX(290px);
		-moz-transform: translateX(290px);
		-ms-transform: translateX(290px);
		-o-transform: translateX(290px);
		transform: translateX(290px);
	}

	.menu-icon {
		button {
			background: transparent;
			border: 0;
			cursor: pointer;
			outline: 0;
			height: 33px;
			width: 33px;
		}

		span {
			display: block;
			background: #FFF;
			border-radius: 5px;
			height: 4px;
			position: relative;
			width: 30px;

			&:before {
				content: '';
				display: block;
				background: #FFF;
				border-radius: 5px;
				height: 4px;
				top: -10px;
				position: absolute;
				width: 30px;
			}

			&:after {
				content: '';
				display: block;
				background: #FFF;
				border-radius: 5px;
				height: 4px;
				top: 10px;
				position: absolute;
				width: 30px;
			}
		}
	}

	.site-logo {
		display: inline-block;

		img {
			max-height: 30px;
		}
	}

	
}

