.desktop-header-3 {
	background: $colordark;
	padding: 20px 0;
	border-bottom: solid 1px rgba(255, 255, 255, 0.1);

	.navbar {
		padding: 0;
	}

	.navbar-brand {
		padding-top: 0;
		padding-bottom: 0;
	}

	.navbar-dark .navbar-nav .nav-link {
		color: #FFF;
	}

	.nav-link {
		font-size: 16px;
		font-weight: 700;
		padding: 0;
		cursor: pointer;

		&.active {
			color: $coloraqua !important;
		}

		&:hover {
			color: $colorpink !important;
		}
	}

	.dropdown-menu {
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 1000;
		float: left;
		min-width: 10rem;
		padding: .5rem 0;
		margin: .125rem 0 0;
		font-size: 1rem;
		color: #212529;
		text-align: left;
		list-style: none;
		background-color: #fff;
		background-clip: padding-box;
		border: 0;
		border-radius: .25rem;
		@extend .shadow-dark;
	}

	button.navbar-toggler:focus {
		outline: 0;
		outline: 0;
	}

	.navbar-nav li:not(:last-child) {
		padding-right: 3rem;
	}

	@media (min-width: 992px) {
		.navbar-expand-lg .navbar-nav .nav-link {
			padding-right: 0;
			padding-left: 0;
		}
	}

	
}